import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Modal, ModalHeader, ModalBody, ModalFooter, Button, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import { LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';
import { renderTimeViewClock } from '@mui/x-date-pickers/timeViewRenderers';
import dayjs from 'dayjs';
import { alert } from '../../components/atoms/Toast';

const ModalExtend = ({ modalExtend, toggleExtend, sppdDetail, onSubmit }) => {
    const [mulai, setMulai] = useState(null);
    const [hingga, setHingga] = useState(null);
    const [alasan, setAlasan] = useState('');

    useEffect(() => {
        if (modalExtend) {
            setMulai(null);
            setHingga(null);
            setAlasan('');
        }
    }, [modalExtend]);

    const handleSubmit = () => {
        if (!mulai || !hingga || !alasan) {
            alert('error', 'Mulai, hingga, dan alasan harus diisi.');
            return;
        }
        if (dayjs(mulai).isAfter(dayjs(hingga))) {
            alert('error', 'Tanggal mulai tidak boleh lebih besar dari tanggal hingga.');
            return;
        }
        if (onSubmit) {
            onSubmit({
                id: sppdDetail?.id,
                mulai: dayjs(mulai).format('YYYY-MM-DD HH:mm:ss'),
                hingga: dayjs(hingga).format('YYYY-MM-DD HH:mm:ss'),
                alasan,
            });
        }
        toggleExtend();
    };

    return (
        <Modal isOpen={modalExtend} toggle={toggleExtend} centered>
            <ModalHeader toggle={toggleExtend}>Perpanjang SPPD</ModalHeader>
            <ModalBody>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                    <Row>
                        <Col md={6}>
                            <FormGroup>
                                <Label for="mulai">Mulai</Label>
                                <DateTimePicker
                                    value={mulai}
                                    onChange={setMulai}
                                    ampm={false}
                                    viewRenderers={{
                                        hours: renderTimeViewClock,
                                        minutes: renderTimeViewClock,
                                    }}
                                    renderInput={(params) => (
                                        <Input
                                            {...params}
                                            id="mulai"
                                            type="text"
                                            style={{ background: '#fff' }}
                                            placeholder="Pilih tanggal mulai"
                                            readOnly
                                        />
                                    )}
                                />
                            </FormGroup>
                        </Col>
                        <Col md={6}>
                            <FormGroup>
                                <Label for="hingga">Hingga</Label>
                                <DateTimePicker
                                    value={hingga}
                                    onChange={setHingga}
                                    ampm={false}
                                    viewRenderers={{
                                        hours: renderTimeViewClock,
                                        minutes: renderTimeViewClock,
                                    }}
                                    renderInput={(params) => (
                                        <Input
                                            {...params}
                                            id="hingga"
                                            type="text"
                                            style={{ background: '#fff' }}
                                            placeholder="Pilih tanggal hingga"
                                            readOnly
                                        />
                                    )}
                                />
                            </FormGroup>
                        </Col>
                    </Row>
                </LocalizationProvider>
                <FormGroup>
                    <Label for="alasan">Alasan</Label>
                    <Input
                        id="alasan"
                        type="textarea"
                        value={alasan}
                        onChange={e => setAlasan(e.target.value)}
                        placeholder="Masukkan alasan perpanjangan"
                        style={{ minHeight: 80 }}
                    />
                </FormGroup>
            </ModalBody>
            <ModalFooter>
                <Button color="primary" onClick={handleSubmit}>Submit</Button>
                <Button color="secondary" onClick={toggleExtend}>Batal</Button>
            </ModalFooter>
        </Modal>
    );
};

ModalExtend.propTypes = {
    modalExtend: PropTypes.bool.isRequired,
    toggleExtend: PropTypes.func.isRequired,
    sppdDetail: PropTypes.shape({
        id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
    onSubmit: PropTypes.func,
};

export default ModalExtend;
